import React, { useEffect, useState } from 'react';
import './ChatArea.css';

const FAQS = [
    { label: 'FAQ', question: '   ' },
    { label: '', question: '  ' },
    { label: '', question: '   ' },
];

export default function ChatArea({ chatAPI }) {
    const [messages, setMessages] = useState([
        { from: 'bot', text: ' ?' },
    ]);
    const [input, setInput] = useState('');
    const [isLoading, setIsLoading] = useState(false);

    const handleSend = async (msg) => {
        if (!msg.trim()) return;

        setIsLoading(true);
        const userMessage = { from: 'user', text: msg };
        setMessages((prev) => [...prev, userMessage]);
        setInput('');

        //   
        let botMsg = { from: 'bot', text: '' };
        setMessages((prev) => [...prev, botMsg]);
        try {
            await chatAPI.sendMessageStream(
                msg,
                (chunk) => {
                    botMsg.text += chunk;
                    setMessages((prev) => {
                        const updated = [...prev];
                        updated[updated.length - 1] = { ...botMsg };
                        return updated;
                    });
                }
            );
        } catch (error) {
            setMessages((prev) => [
                ...prev.slice(0, -1),
                { from: 'bot', text: '.    .' }
            ]);
        } finally {
            setIsLoading(false);
        }
    };


    const handleFAQ = async (question) => {
        if (!chatAPI) return;
        try {
            setIsLoading(true);
            const response = await chatAPI.getFAQ();
            const faq = response.faqs.find(f => f.question === question);
            if (faq) {
                setMessages(prev => [...prev, { from: 'bot', text: faq.answer }]);
            }
        } catch (error) {
            console.error('Error loading FAQ:', error);
        } finally {
            setIsLoading(false);
        }
    };

    // Load chat history on component mount
    useEffect(() => {
        if (!chatAPI) return;

        const loadHistory = async () => {
            try {
                const history = await chatAPI.getHistory();
                setMessages(history.messages || []);
            } catch (error) {
                console.error('Error loading history:', error);
            }
        };

        loadHistory();
    }, [chatAPI]);

    return (
        <div className="chat-area">
            <div className="faq-buttons">
                {FAQS.map((faq) => (
                    <button key={faq.label} onClick={() => handleFAQ(faq.question)}>{faq.label}</button>
                ))}
            </div>
            <div className="chat-messages">
                {isLoading && <div className="loading-spinner"></div>}
                {messages.map((msg, i) => (
                    <div key={i} className={msg.from === 'user' ? 'msg-user' : 'msg-bot'}>
                        {msg.text}
                    </div>
                ))}
            </div>
            <div className="chat-input-row">
                <input
                    type="text"
                    value={input}
                    onChange={e => setInput(e.target.value)}
                    placeholder=" ..."
                    onKeyDown={e => { if (e.key === 'Enter') handleSend(input); }}
                    disabled={isLoading}
                />
                <button onClick={() => handleSend(input)}></button>
            </div>
        </div>
    );
} 